@import "../../../_variables";

.user {
  display: block;
  padding: 20px;
  border-bottom: 1px solid lightgray;

  .avatar {
    width: 100px;
    height: 100px;
    border-radius: 100px;
    overflow: hidden;
    background-position: center;
    background-size: cover;
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px; }

  .description {
    display: inline-block;
    vertical-align: middle;
    width: 200px; }



  .buttons {
    display: inline-block;
    width: 200px; } }

